@use '../base/mixins' as m;
@use '../base/variables' as v;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    @include m.tablet {
        flex-direction: row;
    }

    &__link {
        font-size: 2rem;
        font-weight: 700;
        color: v.$negro;

        &--registrar {
            @include m.boton(v.$primario, v.$blanco);
        }
    }
}